import {Component} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {PageControls} from "@component/wizard/flow-wizard.component";

@WizardPage(
      VsanUiUtils.getString("vsan.services.fileservices.wizard.fileAnalyticsPage.title"),
)
@Component({
   templateUrl: "file-services-file-analytics-page.html",
   styleUrls: ["file-services-file-analytics-page.scss"],
})
export class FileServicesFileAnalyticsPageComponent {
   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   isFileAnalyticsRuntimeSupported: boolean;

   @InputProperty()
   @OutputProperty()
   fileServiceConfig: VsanFileServiceCommonConfig;

   @InputProperty()
   isEdit: boolean; // isEdit = true, when the user started the Edit workflow

   invalidate(): void {
      this.pageControls.rebuildToc();
   }
}
