import {Component, OnInit} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {PageItem} from "@component/wizard/page-item";
import {AffinitySiteLocation} from "@generated/affinity-site-location";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {NetworkInventoryService} from "@generated/network-inventory-service";
import {TaskInfoData} from "@generated/task-info-data";
import {TaskService} from "@generated/task-service";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {VsanFileServiceDomain} from "@generated/vsan-file-service-domain";
import {VsanFileServicePrecheckResult} from "@generated/vsan-file-service-precheck-result";
import {ManagedObject} from "@service/managed-object";
import {TaskMonitorService, TaskStatus} from "@service/task-monitor-service";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";
import {FileServiceUtil} from "../file-service-util";
import {OvfSettings} from "../shared/file-services-ovf-settings.data";
import {IP} from "./ip.data";

@WizardPage(
      VsanUiUtils.getString("vsan.services.fileservices.wizard.rtc.title"),
)
@Component({
   templateUrl: "file-services-review-page.html",
   styleUrls: ["file-services-review-page.scss"],
})
export class FileServicesReviewPage implements OnInit {

   @InputProperty()
   @OutputProperty()
   fileServiceConfig: VsanFileServiceCommonConfig;

   @InputProperty()
   networkName: string;

   @InputProperty()
   isRecreateDomain: boolean;

   @InputProperty(InputProperty.required(false))
   ovfSettings: OvfSettings;

   @InputProperty(InputProperty.required(false))
   downloadTask: ManagedObject;

   @InputProperty()
   protocol: IP = IP.v4;

   @InputProperty()
   capabilityData: VsanCapabilityData;

   @InputProperty()
   isEdit: boolean; // isEdit = true if user clicks the 'Edit' file service button

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   isReEnable: boolean;

   @InputProperty()
   isAdCredentialChanged: boolean = false;

   @InputProperty()
   affinitySiteSupported: boolean;

   @InputProperty()
   isVsanStretchedCluster: boolean;

   @InputProperty()
   ovfVersion: string;

   @InputProperty()
   availableDomains: DomainOrHostData[];

   @InputProperty()
   isRemovingActiveDirectory: boolean;

   @InputProperty()
   advancedPreflightCheckSupported: boolean;

   IP: typeof IP = IP;

   downloadTaskIcon: string;
   downloadTaskMessage: string;

   error: string;
   warnings: string[];
   showNtpWarning: boolean;

   downloadTaskInfoData: TaskInfoData;

   constructor(private networkInventoryService: NetworkInventoryService,
         private fileService: VsanFileServiceConfigService,
         private taskService: TaskService,
         private taskMonitorService: TaskMonitorService) {
   }

   async ngOnInit(): Promise<void> {
      if (this.isAdvancedPreflightCheckAllowed()) {
         (this.pageControls as PageItem).metadata.title =
               VsanUiUtils.getString("vsan.services.fileservices.wizard.precheck.title");
      }
      // display the npt warning only when AD is set.
      this.showNtpWarning = !this.isRemovingActiveDirectory && !!this.fileServiceConfig.domainConfig?.adConfig?.domain;
      // No OVFs are being downloaded during "edit" workflow
      if (!this.isEdit) {
         if (!this.isUploadLocalOvf && this.downloadTask) {
            // disable the finish button first
            this.toggleFinishButton(false);

            // The OVF files are being downloaded to the VC, and we need to monitor this
            // and update the UI accordingly. We only allow user to click the Finish button
            // when the download task completes
            const timer: number = window.setInterval(this.updateDownloadTaskStatus, 1000);

            try {
               await this.taskMonitorService.getAwaitResult(this.downloadTask);
               this.onOvfDownloadComplete();
               this.setDownloadResultInfo("success",
                     VsanUiUtils.getString("vsan.services.fileservices.rtc.download.succeeded"));
            } catch (error) {
               Logger.warn(error);
               // A completed task gets deleted in 10m or so. In case user gets this page 10+ minutes late after
               // the OVF download completes, UI needs to run a preflight check to get the OVF files
               this.checkOvfFiles();
            } finally {
               // stop refreshing the task after download completes
               clearInterval(timer);
            }
         } else {
            // The OVF files have already been downloaded on the VC so we need to show a static message
            const message: string = this.isUploadLocalOvf
                  ? VsanUiUtils.getString("vsan.services.fileservices.rtc.ovf.upload.present")
                  : VsanUiUtils.getString("vsan.services.fileservices.rtc.download.present", this.ovfVersion);
            this.setDownloadResultInfo("success", message);
         }
      }

      if (this.isEdit) {
         if (!this.isAdvancedPreflightCheckAllowed()) {
            return;
         }
         try {
            this.pageControls.setBusyState();
            this.toggleFinishButton(false);
            await this.performPreflightCheck();
         } finally {
            // keep the Finish button disabled if the preflight check API returns any domain configure issue
            if (!this.error) {
               this.toggleFinishButton(true);
            }
            this.pageControls.clearBusyState();
         }
      }
      await this.queryNetworkName();
   }

   /**
    * In re-enable workflow, we don't have network page in the wizard, so the networkName is empty
    */
   private async queryNetworkName(): Promise<void> {
      try {
         const selectedNetwork: ManagedObject = this.fileServiceConfig.network;
         if (!this.networkName && selectedNetwork) {
            this.pageControls.setBusyState();
            this.networkName = await this.networkInventoryService.getNetworkName(selectedNetwork);
         }
      } catch (error) {
         this.pageControls.addValidationErrors([error]);
      } finally {
         this.pageControls.clearBusyState();
      }
   }

   private async performPreflightCheck(): Promise<void> {
      this.error = null;
      this.warnings = null;
      const warning: string[] = [];
      const result: VsanFileServicePrecheckResult =
            await this.fileService.getPrecheckResult(
                  ManagedObject.contextObject, this.getDomainConfigForCheck(), true);
      if (result.domainConfigIssue) {
         this.error = result.domainConfigIssue;
      }
      if (result.domainConfigWarning) {
         warning.push(result.domainConfigWarning);
      }
      if (result.ntpConfigWarning) {
         warning.push(result.ntpConfigWarning);
      }
      if (!VsanUiUtils.isEmpty(warning)) {
         this.warnings = warning;
      }
   }

   private getDomainConfigForCheck(): VsanFileServiceDomain {
      const domainConfig: VsanFileServiceDomain = JSON.parse(JSON.stringify(this.fileServiceConfig.domainConfig));
      // avoid to check the AD settings in edit workflow unless user changes the AD password
      if (this.isEdit && (!domainConfig.adConfig.password || this.isRemovingActiveDirectory)) {
         domainConfig.adConfig = null;
      }
      return domainConfig;
   }

   private isAdvancedPreflightCheckAllowed(): boolean {
      return this.advancedPreflightCheckSupported;
   }

   private updateDownloadTaskStatus = () => {
      this.taskService.getInfo(this.downloadTask).then((taskInfoData: TaskInfoData) => {
         this.downloadTaskInfoData = taskInfoData;
         if (taskInfoData.progress < 100) {
            this.toggleFinishButton(false);
         }
         switch (taskInfoData.status) {
            case TaskStatus.PENDING:
               this.setDownloadResultInfo("info",
                     VsanUiUtils.getString("vsan.services.fileservices.rtc.download.pending"));
               break;
            case TaskStatus.RUNNING:
               this.setDownloadResultInfo("info",
                     VsanUiUtils.getString("vsan.services.fileservices.rtc.download.running"));
               break;
            case TaskStatus.SUCCESS:
               this.setDownloadResultInfo("success",
                     VsanUiUtils.getString("vsan.services.fileservices.rtc.download.succeeded"));
               this.onOvfDownloadComplete();
               break;
            case TaskStatus.ERROR:
               this.setDownloadResultInfo("danger",
                     VsanUiUtils.getString("vsan.services.fileservices.rtc.download.failed"));
               break;
            default:
               Logger.warn("Unknown status: " + taskInfoData.status);
               this.setDownloadResultInfo("warning",
                     VsanUiUtils.getString("vsan.services.fileservices.rtc.download.unknown"));
         }
      }).catch(err => {
         Logger.error("Cannot get task status: " + err);
         this.setDownloadResultInfo("warning",
               VsanUiUtils.getString("vsan.services.fileservices.rtc.download.unknown"));
      });
   };

   private onOvfDownloadComplete(): void {
      // Create a fake taskInfoData because the task is deleted in 10 minutes after it is completed.
      this.downloadTaskInfoData = this.downloadTaskInfoData ||
            TaskInfoData.Factory.create("", "", "", "", 0);
      // There is a chance that the download task finishes between updateDownloadTaskStatus and getAwaitResult
      // Here we should set the progress to 100 as it's already finished, by doing this we can fix PR 2378522
      this.downloadTaskInfoData.progress = 100;
      this.toggleFinishButton(true);
   }

   private async checkOvfFiles(): Promise<void> {
      try {
         this.pageControls.setBusyState();
         const result: VsanFileServicePrecheckResult =
               await this.fileService.getPrecheckResult(
                     ManagedObject.contextObject, this.fileServiceConfig.domainConfig, false);
         if (result.ovfInstalled) {
            this.setDownloadResultInfo("success",
                  VsanUiUtils.getString("vsan.services.fileservices.rtc.download.present", result.ovfInstalled));
            this.onOvfDownloadComplete();
         } else {
            this.setDownloadResultInfo("danger",
                  VsanUiUtils.getString("vsan.services.fileservices.rtc.download.failed"));
         }
      } catch (err) {
         Logger.error(err);
         this.setDownloadResultInfo("danger", err);
         this.toggleFinishButton(false);
      } finally {
         this.pageControls.clearBusyState();
      }
   }

   private setDownloadResultInfo(icon: string, message: string): void {
      this.downloadTaskIcon = icon;
      this.downloadTaskMessage = message;
   }

   get isUploadLocalOvf(): boolean {
      return this.ovfSettings && this.ovfSettings.isManualDownload;
   }

   private toggleFinishButton = (enabled: boolean) => {
      (this.pageControls as PageItem).nextButtonIsDisabled = !enabled;
   };

   getAffinitySiteLabel(site: AffinitySiteLocation): string {
      return FileServiceUtil.getAffinitySiteLabel(site, this.availableDomains);
   }

   getBooleanPropertyLabel = (property: boolean): string => {
      return property ?
            VsanUiUtils.getString("vsan.services.enabled") :
            VsanUiUtils.getString("vsan.services.disabled");
   };
}
