import {Component, OnInit, ViewChild} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {VsanFileServiceOvfProps} from "@generated/vsan-file-service-ovf-props";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {ManagedObject} from "@service/managed-object";
import {VsanHealthPropertyProvider} from "@generated/vsan-health-property-provider";
import {ExternalProxySettingsConfig} from "@generated/external-proxy-settings-config";
import {OvfOption, OvfSettings} from "../shared/file-services-ovf-settings.data";
import {FileServicesOvfSettings} from "../shared/file-services-ovf-settings.component";
import {PageItem} from "@component/wizard/page-item";
import {VsanFileServicePrecheckResult} from "@generated/vsan-file-service-precheck-result";
import {Version} from "@util/version";

@WizardPage(
   VsanUiUtils.getString("vsan.services.fileservices.wizard.ovf.title"),
)
@Component({
   templateUrl: "file-services-ovf-page.html",
})
export class FileServicesOvfPageComponent implements OnInit, OnValidate {

   @InputProperty()
   clusterRef: ManagedObject;

   @InputProperty()
   pageControls: PageControls;

   @OutputProperty()
   downloadTask: ManagedObject;

   @OutputProperty()
   ovfSettings: OvfSettings;

   @OutputProperty()
   ovfVersion: string;

   @ViewChild("ovfFileSelector")
   ovfFileSelector: FileServicesOvfSettings;

   publicOvfProps: VsanFileServiceOvfProps;

   isInternetAccessEnabled: boolean;

   currentOvfStatus: OvfUploadStatus;

   OvfUploadStatus: typeof OvfUploadStatus = OvfUploadStatus;

   displayUseExistingOption: boolean;
   existingOvfVersion: Version;
   fsvmVersion: Version;
   hostVersion: Version;

   constructor(private fileService: VsanFileServiceConfigService, private healthService: VsanHealthPropertyProvider) {
   }

   async ngOnInit(): Promise<void> {
      let ovfProps: VsanFileServiceOvfProps = null;
      let externalProxyData: ExternalProxySettingsConfig = null;
      let precheckResult: VsanFileServicePrecheckResult = null;
      this.pageControls.setBusyState();
      try {
         [ovfProps, externalProxyData, precheckResult] = await Promise.all([
            this.fileService.getPublicOvfProps(this.clusterRef),
            this.healthService.getExternalProxySettings(this.clusterRef),
            this.fileService.getPrecheckResult(this.clusterRef, null, false),
         ]);
         this.publicOvfProps = ovfProps;
         this.isInternetAccessEnabled = externalProxyData.enableInternetAccess;
         this.ovfSettings = new OvfSettings(this.getRecommendedOvfOption(precheckResult), this.publicOvfProps);
         this.displayUseExistingOption = this.ovfSettings.isUseExisting;
         this.existingOvfVersion = new Version(precheckResult.ovfInstalled);
         this.fsvmVersion = new Version(precheckResult.fsvmVersion);
         this.hostVersion = new Version(precheckResult.hostVersion);
         // PR 2694459, user downloaded the OVF in the wizard and then close and reopen the wizard w/o refresh the
         // services view, the existing OVF version is not refreshed, and it's not displayed in the review page.
         // Here we set the ovfVersion again with the latest result.
         this.ovfVersion = precheckResult.ovfInstalled;
         if (!ovfProps.url && this.isInternetAccessEnabled) {
            this.pageControls.addValidationErrors(
                  [VsanUiUtils.getString("vsan.services.fileservices.wizard.ovf.nourl")]);
         }
      } catch (err) {
         this.pageControls.addValidationErrors([err]);
         this.ovfSettings = new OvfSettings(OvfOption.MANUAL, null);
      } finally {
         this.pageControls.clearBusyState();
      }
   }

   private getRecommendedOvfOption(precheckResult: VsanFileServicePrecheckResult): OvfOption {
      if (precheckResult.ovfInstalled) {
         return OvfOption.EXISTING;
      }
      if (!this.isInternetAccessEnabled || VsanUiUtils.isEmpty(this.publicOvfProps.url)) {
         return OvfOption.MANUAL;
      }
      return OvfOption.DOWNLOAD;
   }

   validate(): string[] | Promise<string[]> {
      if (this.ovfSettings.isUseExisting) {
         return null;
      }
      if (!this.ovfSettings.isManualDownload) {
         return this.validateAutoDownloadOvfFiles();
      }

      // Start to validate the manual uploaded files
      const validationResult: string[] | Promise<string[]> = this.ovfFileSelector.validate();
      return (validationResult instanceof Promise)
            ? this.monitorUploadOvfFiles(validationResult)
            : validationResult;
   }

   onUploadError(errors: string[]): void {
      this.currentOvfStatus = OvfUploadStatus.UPLOAD_ERROR;
      (this.pageControls as PageItem).addValidationErrors(errors);
      this.toggleNextButton(true);
   }

   onUploadCancel(): void {
      this.currentOvfStatus = OvfUploadStatus.UPLOAD_CANCELLED;
      this.toggleNextButton(true);
   }

   onUploadComplete(): void {
      this.currentOvfStatus = OvfUploadStatus.UPLOAD_COMPLETED;
   }

   onOvfSettingsChange(): void {
      this.pageControls.rebuildToc();
   }

   private validateAutoDownloadOvfFiles(): string[] | Promise<string[]> {
      // We have to let user to accept the certificate, this is for security reasons
      if (this.ovfSettings.isSecureUrlNotTrusted) {
         return [VsanUiUtils.getString("vsan.services.fileservices.wizard.ovf.ssl.trust.error")];
      }
      if (this.downloadTask) {
         return null;
      }
      return this.startDownloadOvfFiles();
   }

   private startDownloadOvfFiles(): Promise<string[]> {
      this.pageControls.setBusyState();
      return this.registerOvfTask(this.fileService.downloadPublicOvf(this.clusterRef));
   }

   private monitorUploadOvfFiles(validationResult: Promise<string[]>): Promise<string[]> {
      return validationResult.then((errors: string[]) => {
         if (errors && errors.length) {
            return errors;
         }
         // disable the next button to avoid user clicking it again
         this.toggleNextButton(false);

         this.startUploadOvfFiles();
         return this.registerOvfTask(new Promise<any>((accept, reject) => {
            // Check the upload progress every second until it completes
            const intervalClock: number = window.setInterval(() => {
               if (this.currentOvfStatus !== OvfUploadStatus.START_UPLOAD) {
                  clearInterval(intervalClock);
                  if (this.currentOvfStatus === OvfUploadStatus.UPLOAD_COMPLETED) {
                     accept(null);
                  } else if (this.currentOvfStatus === OvfUploadStatus.UPLOAD_ERROR) {
                     reject(null);
                  }
               }
            }, 1000);
         }));
      });
   }

   private toggleNextButton(enabled: boolean): void {
      (this.pageControls as PageItem).nextButtonIsDisabled = !enabled;
   }

   private startUploadOvfFiles(): void {
      this.currentOvfStatus = OvfUploadStatus.START_UPLOAD;
   }

   private registerOvfTask = (result: Promise<ManagedObject>): Promise<string[]> => {
      return new Promise<string[]>((resolve, reject) => {
         result.then((task: ManagedObject) => {
            this.downloadTask = task; // task is null if the OVF file is uploaded from local computer
            this.pageControls.clearBusyState();
            resolve(null);
         }).catch((error: string) => {
            this.pageControls.clearBusyState();
            reject([error]);
         }).finally(() => this.toggleNextButton(true));
      });
   };
}

export enum OvfUploadStatus {
   START_UPLOAD,
   UPLOAD_COMPLETED,
   UPLOAD_CANCELLED,
   UPLOAD_ERROR,
}
