import {Component, OnInit, ViewChild} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {IP} from "./ip.data";
import {VsanFileServiceHostIpSettings} from "@generated/vsan-file-service-host-ip-settings";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {IpAddressesRequestSpec} from "@generated/ip-addresses-request-spec";
import {NetworkService} from "@generated/network-service";
import {IpParserService} from "@service/ip-parser.service";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {AffinitySiteLocation} from "@generated/affinity-site-location";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {FileServiceUtil} from "../file-service-util";
import {VsanFileServicePrecheckResult} from "@generated/vsan-file-service-precheck-result";
import {OvfOption, OvfSettings} from "../shared/file-services-ovf-settings.data";
import {Version} from "@util/version";
import {
   AbstractControl,
   FormArray,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn,
   Validators} from "@angular/forms";
import {ClrForm} from "@clr/angular";
import {ReferenceWatcher} from "@util/reference-watcher";

@WizardPage(
      VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.input.pool.title"),
      null,
      [VsanStretchedClusterService],
)
@Component({
   templateUrl: "file-services-ip-pool-page.html",
   styleUrls: ["file-services-ip-pool-page.scss"],
})
export class FileServicesIpPoolPageComponent implements OnInit, OnValidate {
   private static readonly DNS_SEPARATOR: string = ",";
   // At the first release, the max container limit is 8
   private readonly MAX_CONTAINER_NUMBER_EARLY: number = 8;
   // From 70U1, the limitation is extended to 32
   private readonly MAX_CONTAINER_NUMBER_EXTEND: number = 32;
   // From 70U2, the limitation is extended to 64 if largeScaleSupport is on
   private readonly MAX_CONTAINER_NUMBER_LARGE_SCALE: number = 64;

   private readonly AFFINITY_SITE_SUPPORTED_FS_VERSION: string = "1.2.0";

   readonly ISDEFAULT_ELEMENT_NAME: string = "primary";
   readonly IPADDRESS_ELEMENT_NAME: string = "ipAddress";
   readonly DNSNAME_ELEMENT_NAME: string = "dnsName";
   readonly AFFINITY_ELEMENT_NAME: string = "affinityLocation";

   readonly REQUIRED_VALIDATION: string = "required";
   readonly IPV4_VALIDATION: string = "ipv4Required";
   readonly IPV6_VALIDATION: string = "ipv6Required";
   readonly IP_INVALID_VALIDATION: string = "ipInvalid";
   readonly IP_DUPLICATE_VALIDATION: string = "ipDuplicate";
   readonly IP_SAME_NETWORK_VALIDATION: string = "ipNotSameNetwork";
   readonly DNS_INVALID_VALIDATION: string = "dnsInvalid";
   readonly DNS_DUPLICATE_VALIDATION: string = "dnsDuplicate";
   readonly DNS_AD_VALIATION: string = "dnsEndsWithAdDomain";

   @InputProperty()
   @OutputProperty()
   fileServiceConfig: VsanFileServiceCommonConfig;

   @InputProperty()
   clusterRef: ManagedObject;

   @InputProperty()
   isEdit: boolean; // isEdit = true, when the user started the Edit workflow

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   protocol: IP;

   // user can open the wizard to recreate the FS domain if it was not created successfully before.
   @InputProperty()
   isRecreateDomain: boolean;

   @InputProperty()
   numberOfHosts: number;

   @InputProperty()
   originalDomainName: string;

   @InputProperty(InputProperty.required(false))
   ovfSettings: OvfSettings;

   @InputProperty()
   capabilityData: VsanCapabilityData;

   @InputProperty()
   largeScaleClusterSupported: boolean;

   @OutputProperty()
   affinitySiteSupported: boolean;

   @OutputProperty()
   activeDirectorySupported: boolean;

   @InputProperty()
   @OutputProperty()
   isVsanStretchedCluster: boolean;

   @InputProperty()
   @OutputProperty()
   availableDomains: DomainOrHostData[];

   @OutputProperty()
   advancedPreflightCheckSupported: boolean;

   @ViewChild(ClrForm)
   clrForm: ClrForm;

   affinitySites: AffinitySiteLocation[] = [
         AffinitySiteLocation.PREFERRED_DOMAIN,
         AffinitySiteLocation.NON_PREFERRED_DOMAIN,
         AffinitySiteLocation.NONE];

   originalNumOfIpSettings: number = 0;
   isDnsNameWarningShown: boolean = false;
   isAffinitySiteWarningShown: boolean = false;
   ipSettingsForm: FormGroup;

   private editPrimaryIpSupported: boolean;

   constructor(private networkService: NetworkService,
         private fileService: VsanFileServiceConfigService,
         private ipParser: IpParserService,
         private formBuilder: FormBuilder,
         private refWatcher: ReferenceWatcher) {}

   async ngOnInit(): Promise<void> {
      this.originalNumOfIpSettings = this.ipSettings.length;
      if (this.maxNumberOfContainers && !this.originalNumOfIpSettings) {
         for (let i: number = 0; i < this.maxNumberOfContainers; ++i) {
            this.ipSettings.push(
                  VsanFileServiceHostIpSettings.Factory.create(
                        false, "", "", AffinitySiteLocation.NONE));
         }
         this.originalNumOfIpSettings = this.maxNumberOfContainers;
         // Make the first IP the default one
         this.ipSettings[0].isDefault = true;
      }
      // We need to display as many IP inputs as the number of the hosts, if there are new hosts added
      if (this.maxNumberOfContainers > this.originalNumOfIpSettings) {
         const emptyIpSettings: VsanFileServiceHostIpSettings[] = [];
         for (let i: number = this.ipSettings.length; i < this.maxNumberOfContainers; ++i) {
            emptyIpSettings.push(VsanFileServiceHostIpSettings.Factory.create(
                  false, "", "", AffinitySiteLocation.NONE));
         }
         this.fileServiceConfig.domainConfig.ipSettings = this.ipSettings.concat(emptyIpSettings);
      }

      try {
         this.pageControls.setBusyState();
         this.isAffinitySiteWarningShown = false;
         const preflightCheckResult: VsanFileServicePrecheckResult =
               await this.fileService.getPrecheckResult(
                     ManagedObject.contextObject,
                     this.isEdit && this.originalDomainName ? this.fileServiceConfig.domainConfig : null,
                     false);
         this.affinitySiteSupported = this.isAffinitySiteSupported(preflightCheckResult);
         this.activeDirectorySupported = preflightCheckResult.activeDirectorySupported;
         this.editPrimaryIpSupported = preflightCheckResult.editPrimaryIpSupported;
         this.advancedPreflightCheckSupported = preflightCheckResult.advancedPreflightCheckSupported;
         this.pageControls.rebuildToc();
      } catch (err) {
         Logger.error(err);
         // Display an error banner on the UI to let user know that the affinity site selection is not available
         // because of an error. And we should only display it when it's a stretched cluster with available domains,
         // otherwise we don't display it even the API fails (because right now this check is for affinity site only)
         this.isAffinitySiteWarningShown = this.isVsanStretchedCluster && !VsanUiUtils.isEmpty(this.availableDomains);
         this.affinitySiteSupported = false;
      } finally {
         this.initFormGroup();
         this.pageControls.clearBusyState();
      }
   }

   invalidate(): void {
      this.pageControls.rebuildToc();
   }

   generateIpAddresses = () => {
      // If the new IP address is valid, populate the IP address for all
      // consecutive hosts
      const startingIP: string = this.ipSettingsFormGroups[0].get(this.IPADDRESS_ELEMENT_NAME).value;

      if (!this.protocol.validateAddress(startingIP)) {
         Logger.warn("Empty or invalid starting IP address.");
         return;
      }

      const ipv4RequestSpec: IpAddressesRequestSpec = IpAddressesRequestSpec.Factory.create(
            startingIP,
            this.fileServiceConfig.domainConfig.mask,
            this.ipSettings.length);

      this.pageControls.setBusyState();
      this.networkService.getConsequentHostAddresses(ipv4RequestSpec)
            .then((result: string[]) => {
               this.ipSettingsFormGroups.filter((item: FormGroup, index: number) => index > 0)
                     .forEach(ipSettings => ipSettings.get(this.IPADDRESS_ELEMENT_NAME).setValue(result.shift()));
               this.invalidate();
               this.pageControls.clearBusyState();
            })
            .catch((error: string) => {
               this.pageControls.clearBusyState();
               this.pageControls.addValidationErrors([error]);
            });
   };

   isAddAllowed(): boolean {
      return this.maxNumberOfContainers > this.ipSettingsFormGroups.length;
   }

   addIpConfig(): void {
      const newItem: VsanFileServiceHostIpSettings = VsanFileServiceHostIpSettings.Factory.create(
            false, "", "", AffinitySiteLocation.NONE);
      this.ipSettingsFormControl.push(this.createIpSettingGroup(newItem));
      this.ipSettings.push(newItem);
   }

   removeIpConfig(index: number): void {
      this.ipSettingsFormControl.removeAt(index);
      this.ipSettings.splice(index, 1);
   }

   /**
    * The wizard framework calls this method to check if all the inputs are valid in the current reactive form
    * This is required for the views which has inline validations. Otherwise you have to add an extra error
    * message in the validate method to stop the wizard from going to next view.
    */
   inlineValidate(): boolean {
      this.clrForm.markAsTouched();
      return this.ipSettingsForm.valid;
   }

   validate(): string[] | Promise<string[]> {
      this.pageControls.setBusyState();
      return this.fileService.getPrecheckResult(ManagedObject.contextObject, this.fileServiceConfig.domainConfig, false)
            .then(result => {
               if (result.domainConfigIssue) {
                  return [result.domainConfigIssue];
               }
               return null;
            })
            .catch(err => {
               Logger.error(err, "Failed to validate the configuration on cluster.");
               // we should not block the user action if this API fails here, because it could be a fake issue.
               // If there is really something wrong, the reconfigure FS task eventually fails w/o data loss.
               // return null here so user is able to go to the next page.
               return null;
            })
            .finally(() => {
               this.pageControls.clearBusyState();
            });
   }

   canRemoveIp = (ipSettingsGroup: FormGroup, index: number): boolean => {
      // Cannot remove the primary IP
      const primaryIpControl: FormControl = this.ipSettingsForm.get(this.ISDEFAULT_ELEMENT_NAME) as FormControl;
      if (primaryIpControl.value === index) {
         return false;
      }
      // Allow this if backend API supports - it didn't in 7.0 release, but it does since 70u1
      if (this.capabilityData.isIpRemovalInEditModeSupported) {
         return true;
      }

      // Keep below checks for 7.0 in linked mode

      // Allow user to remove IPs if the domain is not created successfully
      if (this.isRecreateDomain) {
         return true;
      }
      // In edit mode, we only allow to remove the ips for new added hosts
      // The existing IPs are not removable because of the backend API limitation
      return !(this.isEdit && index < this.originalNumOfIpSettings);
   };

   /**
    * Use this method to decide if the ip input element should be displayed
    */
   isEditIpAllowed = (ipSettingsGroup: FormGroup, index: number): boolean => {
      // We allow user to input the IP address when 1) configure the domain,
      // or 2) edit the domain after adding a new host
      // or 3) recreate the file service domain
      // index >= this.originalNumOfIpSettings means this IP input is for the new host
      if (this.isRecreateDomain) {
         return true;
      }

      // In 8.0 we allow user to edit the primary IP of an existing fs domain
      if (this.editPrimaryIpSupported) {
         return true;
      }
      if (this.isEdit) {
         // Cannot edit the primary IP in edit mode
         if (ipSettingsGroup.get(this.ISDEFAULT_ELEMENT_NAME).value) {
            return false;
         }
         // from 7.0 u1, the ip settings can be changed unless it's the primary IP
         return this.capabilityData.isIpRemovalInEditModeSupported
               || index >= this.originalNumOfIpSettings;
      }
      return true;
   };

   isChangePrimaryIpAllowed = (): boolean => {
      // user can change the primary ip if there is no domain yet
      // Start from 8.0 we allow user to change the primary IP of an existing FS domain
      return this.editPrimaryIpSupported || this.isCreatingNewDomain;
   };

   get isCreatingNewDomain(): boolean {
      return this.isRecreateDomain || !this.isEdit;
   }

   async lookupDnsNames(): Promise<void> {
      if (this.hasInvalidIpAddress()) {
         this.pageControls.addValidationErrors(
               [VsanUiUtils.getString("vsan.services.fileservices.wizard.dnslookup.error")]);
         return;
      }
      this.pageControls.clearValidationErrors();
      this.pageControls.setBusyState(VsanUiUtils.getString("vsan.loading"));
      try {
         this.isDnsNameWarningShown = false;
         const dnsNames: string[] =
               await this.fileService.getDnsNames(this.clusterRef, this.ipAddresses, this.dnsServers);
         // When the DNS name(s) cannot be obtained based on the IP addresses and DNS servers,
         // a warning message is displayed.
         this.isDnsNameWarningShown = VsanUiUtils.isEmpty(dnsNames) || dnsNames.every(name => !name);
         if (this.isDnsNameWarningShown) {
            return;
         }

         this.ipSettingsFormGroups.forEach(ipSettingsGroup => {
            let fqdn: string = dnsNames.shift();
            if (fqdn.endsWith(".")) {
               fqdn = fqdn.substr(0, fqdn.length - 1);
            }
            ipSettingsGroup.get(this.DNSNAME_ELEMENT_NAME).setValue(fqdn);
         });
      } catch (error) {
         this.isDnsNameWarningShown = true;
         Logger.error("Unable to obtain FQDN based on IP addresses and DNS servers.");
      } finally {
         this.pageControls.clearBusyState();
      }
   }

   get dnsServers(): string[] {
      return this.fileServiceConfig.domainConfig.dnsServers
            .split(FileServicesIpPoolPageComponent.DNS_SEPARATOR)
            .map(dns => dns.trim())
            .filter(dns => dns);
   }

   isGenerateIpAddressesEnabled(): boolean {
      // Don't allow 'auto fill' in edit mode unless it's recreating fs domain
      return this.isCreatingNewDomain && this.isLookupDnsNameEnabled();
   }

   isLookupDnsNameEnabled(): boolean {
      return this.ipSettingsFormGroups?.some(formGroup =>
                  !VsanUiUtils.isEmpty(formGroup.get(this.IPADDRESS_ELEMENT_NAME).value));
   }

   getAffinitySiteLabel(site: AffinitySiteLocation): string {
      return FileServiceUtil.getAffinitySiteLabel(site, this.availableDomains);
   }

   /**
    * If the ovf file is existing or provided by user, the result from preflight check API is correct
    * If the ovf file is auto-downloaded, we need to check the file service API version
    */
   private isAffinitySiteSupported(preflightCheckResult: VsanFileServicePrecheckResult): boolean {
      if (this.isEdit
            || !this.ovfSettings // ovfSettings is null means the current OVF file is latest
            || [OvfOption.MANUAL, OvfOption.EXISTING].includes(this.ovfSettings.ovfOption)) {
         return preflightCheckResult.affinitySiteSupported;
      } else {
         return new Version(preflightCheckResult.fileServiceVersion)
               .compare(new Version(this.AFFINITY_SITE_SUPPORTED_FS_VERSION)) >= 0;
      }
   }

   private get maxNumberOfContainers(): number {
      return Math.min(this.numberOfHosts, this.maxSupportedNumberOfContainers);
   }

   private get maxSupportedNumberOfContainers(): number {
      if (this.largeScaleClusterSupported) {
         return this.MAX_CONTAINER_NUMBER_LARGE_SCALE;
      } else if (this.activeDirectorySupported) {
         return this.MAX_CONTAINER_NUMBER_EXTEND;
      } else {
         return this.MAX_CONTAINER_NUMBER_EARLY;
      }
   }

   private initFormGroup(): void {
      this.ipSettingsForm = this.formBuilder.group({
         primary: this.ipSettings.findIndex(ipSetting => ipSetting.isDefault),
         ipSettings: new FormArray(this.ipSettings.map(ipSetting => this.createIpSettingGroup(ipSetting))),
      });
      const primaryIpControl: FormControl = this.ipSettingsForm.get(this.ISDEFAULT_ELEMENT_NAME) as FormControl;
      this.refWatcher.watchSubscriptionRef(primaryIpControl.valueChanges.subscribe(index => {
         this.ipSettings.forEach(ipSetting => ipSetting.isDefault = false);
         this.ipSettings[index].isDefault = true;
         this.invalidate();
      }));
      this.ipSettingsFormGroups.forEach(groupControl => {
         this.refWatcher.watchSubscriptionRef(groupControl.valueChanges.subscribe(values => {
            // cannot use the index from the above '.forEach' because the index may be out of the array bounds.
            // In my test, there are 2 groups. After I click the 'Lookup IP' button, it starts from 3.
            const index: number = this.ipSettingsFormGroups.indexOf(groupControl);
            this.ipSettings[index].address = values.ipAddress;
            this.ipSettings[index].dnsName = values.dnsName;
            if (this.affinitySiteSupported) {
               this.ipSettings[index].affinityLocation = values.affinityLocation;
            }
            this.invalidate();
         }));
      });
   }

   /**
    * Validate if the IPs are 1) valid ipv4 format, 2) is unique, and 3) is in the same network with gateway/mask
    * Cannot move to domain-validation.service because 2) & 3) need other domain configurations, which will be
    * unavailable in domain-validation.service
    */
   private ipValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      if (VsanUiUtils.isEmpty(control.value)) {
         return null;
      }
      if (!this.isIpValid(control.value)) {
         return {[this.IP_INVALID_VALIDATION]: {value: control.value}};
      }
      if (!this.isUniqueIp(control.value)) {
         return {[this.IP_DUPLICATE_VALIDATION]: {value: control.value}};
      }
      if (!this.isIpNetworkValid(control.value)) {
         return {[this.IP_SAME_NETWORK_VALIDATION]: {value: control.value}};
      }
      return null;
   };

   /**
    * Validate if the fqdn is 1) unique, 2)doesn't have invalid chars
    * Cannot move to domain-validation.service because 1) needs other domain configurations, which will be
    * unavailable in domain-validation.service
    */
   private dnsNameValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      if (VsanUiUtils.isEmpty(control.value)) {
         return null;
      }
      if (!this.isUniqueFqdn(control.value)) {
         return {[this.DNS_DUPLICATE_VALIDATION]: {value: control.value}};
      }
      if (!this.isValidDnsName(control.value)) {
         return {[this.DNS_INVALID_VALIDATION]: {value: control.value}};
      }
      return null;
   };

   private isIpValid(ip: string): boolean {
      return this.protocol === IP.v4
            ? this.ipParser.isIpv4AddressValid(ip)
            : this.ipParser.isIpv6AddressValid(ip);
   }

   /**
    * Check if the ip is unique in the given inputs
    */
   private isUniqueIp(ipAddress: string): boolean {
      const count: number =
            this.ipSettingsFormGroups
                  ?.filter(formGroup => formGroup.get(this.IPADDRESS_ELEMENT_NAME).value === ipAddress).length;
      return count === 1;
   }

   private isIpNetworkValid(ip: string): boolean {
      if (this.protocol === IP.v6) {
         // The network validation (UI and backend) for ipv6 addresses does not include validation for same ipv6 subnets.
         // Return true to have consistent validations (UI and API), but keep the method to make an easy change if needed.
         // More info in https://bugzilla.eng.vmware.com/show_bug.cgi?id=2583003
         return true;
      }
      const mask: string = this.fileServiceConfig.domainConfig.mask.trim();
      const gateway: string = this.fileServiceConfig.domainConfig.gatewayAddress.trim();
      return this.ipParser.isSameIpv4Subnet(ip, gateway, mask);
   }

   /**
    * Check if the fqdn is unique in the given inputs
    */
   private isUniqueFqdn(fqdn: string): boolean {
      const count: number =
            this.ipSettingsFormGroups
                  ?.filter(formGroup => formGroup.get(this.DNSNAME_ELEMENT_NAME).value === fqdn).length;
      return count === 1;
   }

   private get ipSettings(): VsanFileServiceHostIpSettings[] {
      return this.fileServiceConfig.domainConfig.ipSettings;
   }

   private get adDomain(): string {
      return this.fileServiceConfig.domainConfig.adConfig?.domain?.trim();
   }

   private static readonly DNS_NAME_REGEX: RegExp =
         /(?=^.{4,253}$)(^((?!-)[a-zA-Z0-9-]{0,62}[a-zA-Z0-9]\.)+[a-zA-Z]{2,63}$)/;

   private isValidDnsName(dnsName: string): boolean {
      return FileServicesIpPoolPageComponent.DNS_NAME_REGEX.test(dnsName);
   }

   private createIpSettingGroup(ipSettings: VsanFileServiceHostIpSettings): FormGroup {
      const fields: any = {
         [this.ISDEFAULT_ELEMENT_NAME]: [ipSettings.isDefault],
         [this.IPADDRESS_ELEMENT_NAME]: [ipSettings.address, [Validators.required, this.ipValidator]],
         [this.DNSNAME_ELEMENT_NAME]: [ipSettings.dnsName, [Validators.required, this.dnsNameValidator]],
      };
      if (this.affinitySiteSupported) {
         fields[this.AFFINITY_ELEMENT_NAME] = [ipSettings.affinityLocation];
      }
      return this.formBuilder.group(fields);
   }

   get ipSettingsFormGroups(): FormGroup[] {
      return this.ipSettingsFormControl?.controls as FormGroup[];
   }

   private hasInvalidIpAddress(): boolean {
      return this.ipSettingsFormGroups?.some(formGroup => formGroup.get(this.IPADDRESS_ELEMENT_NAME).invalid);
   }

   private get ipSettingsFormControl(): FormArray {
      return this.ipSettingsForm?.controls?.ipSettings as FormArray;
   }

   private get ipAddresses(): string[] {
      return this.ipSettingsFormGroups?.map(ipSetting => ipSetting.get(this.IPADDRESS_ELEMENT_NAME).value.trim());
   }
}
