import {Directive, ElementRef, AfterViewInit, Input} from '@angular/core';
import {Icon} from "@util/icon";

/**
 * Attribute directive for setting the title of clr-icon according to its shape.
 */
@Directive({
   selector: "clr-icon"
})
export class IconTitleDirective implements AfterViewInit {

   private readonly SHAPE_ATTRIBUTE: string = "shape";
   private readonly TITLE_ATTRIBUTE: string = "title";

   @Input("vsan-icon-title")
   iconTitle: string;

   constructor(private el: ElementRef) { }

   ngAfterViewInit() {
      // Return if clr-icon already has title attribute.
      if (this.el.nativeElement.getAttribute(this.TITLE_ATTRIBUTE)) {
         return;
      }

      let title: string = this.iconTitle;
      if (!title) {
         const shape: string = this.el.nativeElement.getAttribute(this.SHAPE_ATTRIBUTE);
         title = Icon.getDefaultVsanIconTitle(shape);
      }
      if (title) {
         this.el.nativeElement.setAttribute(this.TITLE_ATTRIBUTE, title);
      }
   }
}