import {AffinitySiteLocation} from "@generated/affinity-site-location";
import {VsanUiUtils} from "@util/vsan-util";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {IpSettingData} from "@component/configurations/file-service-configuration.component";
import {VsanFileServiceHostIpSettings} from "@generated/vsan-file-service-host-ip-settings";
import {VsanVdfsConfig} from "@generated/vsan-vdfs-config";

/**
 * Holds the common properties and methods used for file service UI
 */
export class FileServiceUtil {
   // properties used for API fileServiceConfigService.getFileShareAttributes
   static readonly SHARE_NAME_PROPERTY: string = "sharenames";
   static readonly SHARE_UUID_PROPERTY: string = "shareuuids";
   static readonly SHARE_FILE_INDEXING_ENABLED_PROPERTY: string = "fileIndexingEnabled";

   static getAffinitySiteLabel(site: AffinitySiteLocation, availableDomains: DomainOrHostData[]): string {
      switch (site) {
         case AffinitySiteLocation.PREFERRED_DOMAIN:
            const preferredDomain: DomainOrHostData = availableDomains.find(domain => domain.isPreferred);
            return preferredDomain ?
                  VsanUiUtils.getString("vsan.fileservices.affinitySite.preferred.label", preferredDomain.label):
                  VsanUiUtils.getString("vsan.cluster.configure.summary.preferredFd");
         case AffinitySiteLocation.NON_PREFERRED_DOMAIN:
            const secondaryDomain: DomainOrHostData =
                  availableDomains.find(domain => !domain.isPreferred && !domain.isHost);
            return secondaryDomain ?
                  VsanUiUtils.getString("vsan.fileservices.affinitySite.nonPreferred.label", secondaryDomain.label) :
                  VsanUiUtils.getString("vsan.cluster.configure.summary.secondaryFd");
         default:
            return VsanUiUtils.getString("vsan.fileservices.affinitySite.none.label");
      }
   }

   /**
    * Returns the IP addresses from the File Service configuration in the form of a string, separated by commas.
    */
   static getIpSettings(vdfsConfig: VsanVdfsConfig, availableDomains: DomainOrHostData[]): IpSettingData[] {
      if (!vdfsConfig
            || !vdfsConfig.config
            || !vdfsConfig.config.domainConfig
            || !vdfsConfig.config.domainConfig.ipSettings) {
         return [];
      }

      return vdfsConfig.config.domainConfig.ipSettings.map((ipSetting: VsanFileServiceHostIpSettings) => {
         return {
            ip: this.getIpLabel(ipSetting),
            fqdn: ipSetting.dnsName,
            affinitySite: FileServiceUtil.getAffinitySiteLabel(ipSetting.affinityLocation, availableDomains),
         };
      });
   }

   private static getIpLabel(ipSetting: VsanFileServiceHostIpSettings): string {
      return ipSetting.isDefault
            ? VsanUiUtils.getString("vsan.services.fileservices.primaryIp", ipSetting.address)
            : ipSetting.address;
   }
}
