import {Component, ViewChild} from "@angular/core";
import {
   AbstractControl,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn,
   Validators,
} from "@angular/forms";
import {ClrForm} from "@clr/angular";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {ActiveDirectoryConfig} from "@generated/active-directory-config";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {VsanFileServicePrecheckResult} from "@generated/vsan-file-service-precheck-result";
import {IpParserService} from "@service/ip-parser.service";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {ReferenceWatcher} from "@util/reference-watcher";
import {VsanUiUtils} from "@util/vsan-util";
import {OvfOption, OvfSettings} from "../shared/file-services-ovf-settings.data";
import {DomainValidationService} from "./domain-validation.service";

@WizardPage(
      VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.title"),
)
@Component({
   templateUrl: "file-services-domain-page.html",
   styleUrls: ["file-services-domain-page.scss"],
})
export class FileServicesDomainPageComponent implements OnValidate {
   private static readonly PASSWORD_PLACEHOLDER: string = "********";
   private readonly AD_SUPPORTED_FS_VERSION: string = "1.1.0";

   readonly DOMAIN_NAME_ELEMENT_NAME: string = "domain-name";
   readonly DNS_SERVERS_ELEMENT_NAME: string = "dns-servers";
   readonly DNS_SUFFIX_ELEMENT_NAME: string = "dns-suffix";
   readonly ENABLE_AD_ELEMENT_NAME: string = "enable-active-directory";
   readonly AD_DOMAIN_NAME_ELEMENT_NAME: string = "ad-domain";
   readonly OU_ELEMENT_NAME: string = "organizational-unit";
   readonly AD_USERNAME_ELEMENT_NAME: string = "username";
   readonly AD_PASSWORD_ELEMENT_NAME: string = "password";

   @InputProperty()
   private fileSharesCount: number;

   @InputProperty()
   @OutputProperty()
   fileServiceConfig: VsanFileServiceCommonConfig;

   @InputProperty()
   isEdit: boolean; // isEdit = true, when the user started the Edit workflow

   @InputProperty()
   pageControls: PageControls;

   @InputProperty(InputProperty.required(false))
   ovfSettings: OvfSettings;

   @InputProperty()
   capabilityData: VsanCapabilityData;

   @InputProperty()
   hasAdConfigured: boolean;

   @OutputProperty()
   originalDomainName: string;

   @OutputProperty()
   isAdCredentialChanged: boolean = false;

   @OutputProperty()
   activeDirectorySupported: boolean;

   @OutputProperty()
   get isRemovingActiveDirectory(): boolean {
      return this.hasAdConfigured && !this.activeDirectoryEnabled;
   }

   @ViewChild(ClrForm)
   clrForm: ClrForm;

   dnsServerAvailable: boolean;

   // Use this flag to control if user can change the checkbox and the domain input
   allowEditActiveDirectory: boolean;

   domainForm: FormGroup;

   loading: boolean;

   private adConfigBackup: ActiveDirectoryConfig;

   get activeDirectoryEnabled(): boolean {
      return this._activeDirectoryEnabled;
   }

   set activeDirectoryEnabled(val: boolean) {
      this._activeDirectoryEnabled = val;
      if (!val) {
         this.adConfigBackup = this.fileServiceConfig.domainConfig.adConfig;
         this.fileServiceConfig.domainConfig.adConfig = ActiveDirectoryConfig.Factory.create("", "", "", "");
      } else {
         this.fileServiceConfig.domainConfig.adConfig = this.adConfigBackup;
      }
      this.resetAdInputValues();
   }

   private _activeDirectoryEnabled: boolean;

   constructor(private ipParser: IpParserService,
         private fileServiceConfigService: VsanFileServiceConfigService,
         public validationService: DomainValidationService,
         private formBuilder: FormBuilder,
         private refWatcher: ReferenceWatcher) {
   }

   async ngOnInit(): Promise<void> {
      this.pageControls.setBusyState();
      this.originalDomainName = this.fileServiceConfig.domainConfig.name;
      this.dnsServerAvailable = this.capabilityData.isNfsv3ProtocolSupported;
      this.adConfigBackup = this.fileServiceConfig.domainConfig.adConfig;
      this.activeDirectoryEnabled = !!this.adDomain;
      this.allowEditActiveDirectory = !this.activeDirectoryEnabled || !this.fileSharesCount;
      try {
         this.loading = true;
         const preflightCheckResult: VsanFileServicePrecheckResult =
               await this.fileServiceConfigService.getPrecheckResult(
                     ManagedObject.contextObject,
                     this.isEdit && this.originalDomainName ? this.fileServiceConfig.domainConfig : null, false);
         this.activeDirectorySupported = this.isActiveDirectorySupported(preflightCheckResult);
      } catch (err) {
         Logger.error(err);
         this.activeDirectorySupported = false;
      } finally {
         this.pageControls.clearBusyState();
         this.initFormGroup();
         this.loading = false;
      }
   }

   invalidate(): void {
      this.pageControls.rebuildToc();
   }

   adCredentialChanged(): void {
      this.isAdCredentialChanged = true;
      this.addPasswordValidator();
   }

   get passwordPlaceholder(): string {
      return this.isEdit && this.hasAdConfigured ?
            FileServicesDomainPageComponent.PASSWORD_PLACEHOLDER : "";
   }

   /**
    * The wizard framework calls this method to check if all the inputs are valid in the current reactive form
    * This is required for the views which has inline validations. Otherwise you have to add an extra error
    * message in the validate method to stop the wizard from going to next view.
    */
   inlineValidate(): boolean {
      this.clrForm.markAsTouched();
      return this.domainForm.valid;
   }

   validate(): string[] | Promise<string[]> {
      // Return null directly because we only have inline validation in this view
      return null;
   }

   get editDomainWarning(): string {
      return this.originalDomainName
            ? VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.warning")
            : VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.noDomain");
   }

   private get adDomain(): string {
      return this.fileServiceConfig.domainConfig.adConfig.domain;
   }

   private get adUsername(): string {
      return this.fileServiceConfig.domainConfig.adConfig.username;
   }

   /**
    * If the ovf file is existing or provided by user, the result from preflight check API is correct
    * If the ovf file is auto-downloaded, we need to check the file service API version
    */
   private isActiveDirectorySupported(preflightCheckResult: VsanFileServicePrecheckResult): boolean {
      if (this.isEdit
            || !this.ovfSettings
            || [OvfOption.MANUAL, OvfOption.EXISTING].includes(this.ovfSettings.ovfOption)) {
         return preflightCheckResult.activeDirectorySupported;
      } else {
         return preflightCheckResult.fileServiceVersion >= this.AD_SUPPORTED_FS_VERSION;
      }
   }

   private initFormGroup(): void {
      const formSettings: any = {
         [this.DOMAIN_NAME_ELEMENT_NAME]: new FormControl(
               this.originalDomainName,
               [this.validationService.domainNameValidator]),
      };

      if (this.dnsServerAvailable) {
         formSettings[this.DNS_SERVERS_ELEMENT_NAME] = new FormControl(
               this.fileServiceConfig.domainConfig.dnsServers,
               [this.validationService.dnsServerValidator]);
         formSettings[this.DNS_SUFFIX_ELEMENT_NAME] = new FormControl(
               this.fileServiceConfig.domainConfig.dnsSuffixes,
               [this.validationService.dnsSuffixValidator]);
      }
      if (this.activeDirectorySupported) {
         formSettings[this.ENABLE_AD_ELEMENT_NAME] = new FormControl(
               {disabled: !this.allowEditActiveDirectory, value: this.activeDirectoryEnabled});
         formSettings[this.AD_DOMAIN_NAME_ELEMENT_NAME] = new FormControl(
               {disabled: this.hasAdConfigured, value: this.adDomain},
               [this.adDomainSuffixValidator]);
         formSettings[this.OU_ELEMENT_NAME] = new FormControl(
               {
                  disabled: this.hasAdConfigured,
                  value: this.fileServiceConfig.domainConfig.adConfig.organizationalUnit,
               },
               [Validators.maxLength(255), this.validationService.ouValidator]);
         formSettings[this.AD_USERNAME_ELEMENT_NAME] = new FormControl(
               {disabled: this.hasAdConfigured, value: this.fileServiceConfig.domainConfig.adConfig.username},
               [this.validationService.usernameValidator]);
         formSettings[this.AD_PASSWORD_ELEMENT_NAME] = new FormControl(
               this.fileServiceConfig.domainConfig.adConfig.password,
               this.hasAdConfigured ? null : [Validators.required]);
      }
      this.domainForm = this.formBuilder.group(formSettings);
      this.bindControlValues();
   }

   private adDomainSuffixValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const adDomain: string = control.value;
      if (this.getAdDomainValidationResult(adDomain)) {
         return {[this.validationService.INVALID_AD_DOMAIN_VALIDATION]: {value: adDomain}};
      }
      return null;
   };

   getAdDomainValidationResult(adDomain: string): string {
      const result: string = this.validationService.getAdDomainValidationResult(adDomain);
      return result
            ? result
            : this.getAdDomainSuffixCheckResult(adDomain);
   }

   private getAdDomainSuffixCheckResult(adDomain: string): string {
      if (this.fileServiceConfig.domainConfig.dnsSuffixes.split(",")
            .map(suffix => suffix.trim())
            .includes(adDomain)) {
         return null;
      }
      return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.suffix.noAD");
   }

   private toggleAdValidation(enabled: boolean): void {
      if (!enabled) {
         this.adDomainNameControl.disable();
         this.organizationalUnitControl.disable();
         this.adUsernameControl.disable();
         this.adPasswordControl.disable();
         this.adPasswordControl.setValidators([]);
      } else if (!this.hasAdConfigured) {
         // don't allow to change AD settings if it's original enabled
         this.adDomainNameControl.enable();
         this.organizationalUnitControl.enable();
         this.adUsernameControl.enable();
         this.adPasswordControl.enable();
         this.addPasswordValidator();
      }
   }

   private resetAdInputValues(): void {
      if (!this.domainForm) {
         return;
      }
      const adConfig: ActiveDirectoryConfig = this.fileServiceConfig.domainConfig.adConfig;
      this.adDomainNameControl.setValue(adConfig.domain);
      this.organizationalUnitControl.setValue(adConfig.organizationalUnit);
      this.adUsernameControl.setValue(adConfig.username);
      this.adPasswordControl.setValue(adConfig.password);

   }

   private addPasswordValidator(): void {
      this.adPasswordControl.setValidators([Validators.required]);
   }

   private bindControlValues(): void {
      this.refWatcher.watchSubscriptionRef(this.domainForm.valueChanges.subscribe(whatever => this.invalidate()));
      this.refWatcher.watchSubscriptionRef(
            this.domainControl.valueChanges.subscribe(val => this.fileServiceConfig.domainConfig.name = val));
      if (this.dnsServerAvailable) {
         this.refWatcher.watchSubscriptionRef(
               this.dnsControl.valueChanges.subscribe(val => this.fileServiceConfig.domainConfig.dnsServers = val));
         this.refWatcher.watchSubscriptionRef(
               this.dnsSuffixControl.valueChanges.subscribe(val => this.fileServiceConfig.domainConfig.dnsSuffixes = val));
      }
      if (this.activeDirectorySupported) {
         this.refWatcher.watchSubscriptionRef(
               this.enableAdCheckboxControl.valueChanges.subscribe(checked => {
                  this.activeDirectoryEnabled = checked;
                  this.toggleAdValidation(checked);
               }));
         this.refWatcher.watchSubscriptionRef(
               this.adDomainNameControl.valueChanges
                     .subscribe(val => this.fileServiceConfig.domainConfig.adConfig.domain = val));
         this.refWatcher.watchSubscriptionRef(
               this.organizationalUnitControl.valueChanges
                     .subscribe(val => {
                        if (val !== this.fileServiceConfig.domainConfig.adConfig.organizationalUnit) {
                           this.fileServiceConfig.domainConfig.adConfig.organizationalUnit = val;
                           this.adCredentialChanged();
                        }
                     }));
         this.refWatcher.watchSubscriptionRef(
               this.adUsernameControl.valueChanges
                     .subscribe(val => {
                        if (val !== this.fileServiceConfig.domainConfig.adConfig.username) {
                           this.fileServiceConfig.domainConfig.adConfig.username = val;
                           this.adCredentialChanged();
                        }
                     }));
         this.refWatcher.watchSubscriptionRef(
               this.adPasswordControl.valueChanges
                     .subscribe(val => {
                        if (val) {
                           this.fileServiceConfig.domainConfig.adConfig.password = val;
                           this.adCredentialChanged();
                        }
                     }));
      }
   }

   get domainControl(): FormControl {
      return this.domainForm.get(this.DOMAIN_NAME_ELEMENT_NAME) as FormControl;
   }

   get dnsControl(): FormControl {
      return this.domainForm.get(this.DNS_SERVERS_ELEMENT_NAME) as FormControl;
   }

   get dnsSuffixControl(): FormControl {
      return this.domainForm.get(this.DNS_SUFFIX_ELEMENT_NAME) as FormControl;
   }

   get enableAdCheckboxControl(): FormControl {
      return this.domainForm.get(this.ENABLE_AD_ELEMENT_NAME) as FormControl;
   }

   get adDomainNameControl(): FormControl {
      return this.domainForm.get(this.AD_DOMAIN_NAME_ELEMENT_NAME) as FormControl;
   }

   get organizationalUnitControl(): FormControl {
      return this.domainForm.get(this.OU_ELEMENT_NAME) as FormControl;
   }

   get adUsernameControl(): FormControl {
      return this.domainForm.get(this.AD_USERNAME_ELEMENT_NAME) as FormControl;
   }

   get adPasswordControl(): FormControl {
      return this.domainForm.get(this.AD_PASSWORD_ELEMENT_NAME) as FormControl;
   }
}
