import {Logger} from "../../common/util/logger";
import {VsanFileServiceIpType} from "@generated/vsan-file-service-ip-type";

export class IP {
   static readonly v4: IP = new IP("ipv4",
         "vsan.services.fileservices.wizard.ipPool.input.vlan.protocol.ipv4",
         VsanFileServiceIpType.V4,
         /^(?:(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)\.){3}(?:25[0-5]|2[0-4][0-9]|[01]?[0-9][0-9]?)$/);
   static readonly v6: IP = new IP("ipv6",
         "vsan.services.fileservices.wizard.ipPool.input.vlan.protocol.ipv6",
         VsanFileServiceIpType.V6,
         /^((?=.*::)(?!.*::.+::)(::)?([\dA-F]{1,4}:(:|\b)|){5}|([\dA-F]{1,4}:){6})((([\dA-F]{1,4}((?!\3)::|:\b|$))|(?!\2\3)){2}|(((2[0-4]|1\d|[1-9])?\d|25[0-5])\.?\b){4})$/i);

   constructor(private _value: string,
         private keyToLabel: string,
         private vsanIpType: VsanFileServiceIpType,
         private matcher: RegExp) {
   }

   get value(): string {
      return this._value;
   }

   toString(): string {
      return this.keyToLabel;
   }

   static parse(val: string) {
      switch (val) {
         case IP.v4.value:
            return IP.v4;
         case IP.v6.value:
            return IP.v6;
         default:
            Logger.warn("Unknown protocol: " + val);
            return null;
      }
   }

   toVsanIpVersion() {
      return this.vsanIpType;
   }

   validateAddress(address: string) {
      return address && address != "" && this.matcher.test(address);
   }
}

