import {Component, OnInit} from "@angular/core";
import {PageSet} from "@component/wizard/flow-wizard.component";
import {ActiveDirectoryConfig} from "@generated/active-directory-config";
import {FileAnalyticsConfigService} from "@generated/file-analytics-config-service";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {VsanFileServiceDomain} from "@generated/vsan-file-service-domain";
import {VsanFileServiceIpType} from "@generated/vsan-file-service-ip-type";
import {VsanFileServiceOvf} from "@generated/vsan-file-service-ovf";
import {FileServicesConfigureWizardModalContext} from "@navigation/context/file-services-configure-wizard-modal-context.data";
import {Network} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ManagedObject} from "@service/managed-object";
import {WebPlatform} from "@util/platform";
import {VsanUiUtils} from "@util/vsan-util";
import {FileServicesDomainPageComponent} from "./file-services-domain-page.component";
import {FileServicesFileAnalyticsPageComponent} from "./file-services-file-analytics-page.component";
import {FileServicesIntroductionPageComponent} from "./file-services-introduction-page.component";
import {FileServicesIpPoolPageComponent} from "./file-services-ip-pool-page.component";
import {FileServicesNetworkPageComponent} from "./file-services-network-page.component";
import {FileServicesOvfPageComponent} from "./file-services-ovf-page.component";
import {FileServicesReviewPage} from "./file-services-review-page.component";

@Component({
   selector: "vsan-file-services-wizard",
   template: `
      <vsan-flow-wizard
            [title]="title"
            [pageSets]="pages"
            [contextProperties]="context"
            [startingTaskProperty]="taskProperty"
            [services]="services">
      </vsan-flow-wizard>`,
})
export class FileServicesConfigureWizardComponent implements OnInit {
   private readonly DEFAULT_PREFIX_LENGTH: number = 64;

   services: any[] = [VsanFileServiceConfigService, FileAnalyticsConfigService];
   pages: PageSet[];
   context: any = {};
   title: string;
   taskProperty: string = "configureFileService";

   constructor(private fileService: VsanFileServiceConfigService,
         private dataService: DataServiceClient) {
   }

   async ngOnInit(): Promise<void> {
      const modalContext: FileServicesConfigureWizardModalContext =
            VsanUiUtils.getModalContext() as FileServicesConfigureWizardModalContext;

      let downloadingOvf: VsanFileServiceOvf;
      if (!modalContext.isEdit && !modalContext.hasLastestOvf) {
         downloadingOvf = await this.getDownloadingOvf(modalContext);
      }
      this.createInitialContext(modalContext, downloadingOvf ? downloadingOvf.downloadTask : null);
      this.initWizardPages(modalContext, downloadingOvf);
   }

   private async getDownloadingOvf(modalContext: FileServicesConfigureWizardModalContext): Promise<VsanFileServiceOvf> {
      try {
         const ovfs: VsanFileServiceOvf[] = await this.fileService.getRegisteredOvfs(modalContext.clusterRef);
         return ovfs.find(ovf => !!ovf.downloadTask);
      } catch (err) {
         return null;
      }
   }

   private async initWizardPages(modalContext: FileServicesConfigureWizardModalContext,
         downloadingOvf: VsanFileServiceOvf): Promise<void> {
      const pageSet: any[] = [];
      if (modalContext.isEdit) {
         this.title = VsanUiUtils.getString("vsan.services.fileservices.wizard.edit.title");
         this.taskProperty = "editFileServiceConfig";
      } else {
         this.title = VsanUiUtils.getString("vsan.services.fileservices.wizard.configure.title");
         if (!this.context.isReEnable) {
            pageSet.push(FileServicesIntroductionPageComponent);
         }
      }
      // assign a11y title here due to additional title logic
      WebPlatform.setAccessibilityTitle(this.title);
      // Display the ovf page when the current OVF version is not the latest
      if (!modalContext.isEdit && !downloadingOvf && !modalContext.hasLastestOvf) {
         pageSet.push(FileServicesOvfPageComponent);
      }
      if (!this.context.isReEnable) {
         pageSet.push(FileServicesNetworkPageComponent);
         pageSet.push(FileServicesDomainPageComponent);
         pageSet.push(FileServicesIpPoolPageComponent);
      } else if (!await this.networkExists(modalContext.config.network)) {
         pageSet.push(FileServicesNetworkPageComponent);
      }
      if (this.context.capabilityData.isFileAnalyticsSupported) {
         pageSet.push(FileServicesFileAnalyticsPageComponent);
      }
      pageSet.push(FileServicesReviewPage);

      this.pages = [new PageSet(pageSet)];
   }

   private createInitialContext(modalContext: FileServicesConfigureWizardModalContext,
         ovfDownloadTask: ManagedObject = null): void {
      let config: VsanFileServiceCommonConfig = modalContext.config;
      if (!config) {
         config = VsanFileServiceCommonConfig.Factory.create(null, null, false, false);
      }

      if (!config.domainConfig) {
         config.domainConfig = this.createEmptyDomainConfig();
      }
      if (!config.domainConfig.adConfig) {
         config.domainConfig.adConfig = this.createEmptyActiveDirectoryConfig();
      }
      this.context.clusterRef = modalContext.clusterRef;
      this.context.fileServiceConfig = config;
      this.context.isEdit = modalContext.isEdit;
      this.context.capabilityData = modalContext.capabilityData;
      this.context.isRecreateDomain = this.context.isEdit && !config.domainConfig.name;
      if (ovfDownloadTask) {
         this.context.downloadTask = ovfDownloadTask;
      }
      // Use this attr to indicate if we are going to enable file service w/ an existing FS domain
      this.context.isReEnable = !this.context.isEdit
            && !!config.domainConfig.name
            && VsanUiUtils.isEmpty(modalContext.lastDomainConfigErrors);
      this.context.fileSharesCount = modalContext.fileSharesCount;
      this.context.ovfVersion = modalContext.ovfVersion;
      this.context.largeScaleClusterSupported = modalContext.largeScaleClusterSupported;
      this.context.hasAdConfigured = modalContext.hasAdConfigured;
   }

   private createEmptyDomainConfig(): VsanFileServiceDomain {
      return VsanFileServiceDomain.Factory.create(
            "",
            null,
            this.DEFAULT_PREFIX_LENGTH,
            "",
            "",
            "",
            "",
            "",
            VsanFileServiceIpType.V4,
            []);
   }

   private createEmptyActiveDirectoryConfig(): ActiveDirectoryConfig {
      return ActiveDirectoryConfig.Factory.create("", "", "", "");
   }

   /**
    * SR 2664146: UI needs to check if the network still exists in the re-enable case.
    * If not, UI will allow user to choose the network again
    */
   private async networkExists(networkRef: ManagedObject): Promise<boolean> {
      if (!networkRef) {
         return false;
      }
      const networkName: string = await this.dataService.getProperty(Network.name, networkRef);
      return !!networkName;
   }
}
