import {VsanUiUtils} from "@util/vsan-util";
import {Injectable} from "@angular/core";
import {IpParserService} from "@service/ip-parser.service";
import {VsanFileShareUtils} from "../shares/file-share-util";
import {AbstractControl, ValidationErrors, ValidatorFn } from "@angular/forms";

/**
 * Service class which contains the validation logic used in the FileServicesDomainPageComponent
 */
@Injectable()
export class DomainValidationService {
   private readonly OU_SEPARATOR: string = ",";
   private readonly ORGANIZATIONAL_UNIT_HIERARCHICAL_SYMBOL: string = "/";
   // The max allowed length of the whole ou input
   private readonly ORGANIZATIONAL_UNIT_INPUT_MAXIMUM_LENGTH: number = 255;
   // The max length of the value of ou part, for example if you user inputs `cn=something,ou=org,dc=example,dc=com`,
   // the whole string cannot have more than 255 chars, the ou value cannot have more than 64 chars
   private readonly ORGANIZATIONAL_UNIT_VALUE_MAXIMUM_LENGTH: number = 64;
   private readonly DOMAIN_NAME_MAXIMUM_LENGTH: number = 80;
   private readonly OU_PREFIX: string = "ou=";
   private static readonly VALID_AD_CHARS: RegExp = /^[A-Za-z0-9\-._]*$/;
   private static readonly DNS_SEPARATOR: string = ",";

   private static readonly INVALID_DOMAIN_NAME_START: RegExp = /^[0-9-._]/;
   private static readonly INVALID_DOMAIN_NAME_CHARS: RegExp = /[!"#$%&'()*+,/:;<=>?@[\]^`{|}~]/;

   readonly INVALID_NAME_VALIDATION: string = "invalidName";
   readonly INVALID_DNS_VALIDATION: string = "invalidDnsServer";
   readonly INVALID_SUFFIX_VALIDATION: string = "invalidSuffix";
   readonly INVALID_AD_DOMAIN_VALIDATION: string = "invalidAdDomain";
   readonly INVALID_OU_VALIDATION: string = "invalidOu";
   readonly INVALID_USERNAME_VALIDATION: string = "invalidUsername";
   readonly INVALID_MASK_VALIDATION: string = "invalidMask";
   readonly INVALID_PREFIX_LENGTH_VALIDATION: string = "invalidPrefixLength";

   constructor(private ipParser: IpParserService) {}

   domainNameValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const domainName: string = control.value;
      if (this.getDomainNameValidationResult(domainName)) {
         return {[this.INVALID_NAME_VALIDATION]: {value: domainName}};
      }
      return null;
   };

   getDomainNameValidationResult(domainName: string): string {
      if (!domainName) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.noname");
      }
      if (domainName.length > this.DOMAIN_NAME_MAXIMUM_LENGTH) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.domain.maxlength.error");
      }
      if (DomainValidationService.INVALID_DOMAIN_NAME_START.test(domainName)) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.domain.invalidStart");
      }
      if (DomainValidationService.INVALID_DOMAIN_NAME_CHARS.test(domainName)) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.domain.invalidChars");
      }
      return null;
   }

   dnsServerValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const dnsServers: string = control.value;
      if (this.getDnsServerValidationResult(dnsServers)) {
         return {[this.INVALID_DNS_VALIDATION]: {value: dnsServers}};
      }
      return null;
   };

   getDnsServerValidationResult(dnsServerStr: string): string {
      if (!dnsServerStr) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.input.dnsServer.empty");
      }
      const dnsServers: string[] = dnsServerStr.split(DomainValidationService.DNS_SEPARATOR);
      const invalidDnsServers: string[] = dnsServers.filter(dns =>
            !this.ipParser.isIpv4AddressValid(dns) && !this.ipParser.isIpv6AddressValid(dns));
      if (!VsanUiUtils.isEmpty(invalidDnsServers)) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.input.dnsServer.error");
      }
      return null;
   }

   dnsSuffixValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const dnsSuffixes: string = control.value;
      if (this.getDnsSuffixValiationResult(dnsSuffixes)) {
         return {[this.INVALID_SUFFIX_VALIDATION]: {value: dnsSuffixes}};
      }
      return null;
   };

   getDnsSuffixValiationResult(dnsSuffixes: string): string {
      if (!dnsSuffixes) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.input.dnsSuffix.empty");
      }
      const suffixes: string[] = dnsSuffixes.split(DomainValidationService.DNS_SEPARATOR)
            .filter(suffix => suffix.trim()); // filter the empty dns suffix. User input could be 'example.com,'
      const invalidSuffixes: string[] = suffixes.filter(suffix => !this.validateSingleSuffix(suffix.trim()));
      if (!VsanUiUtils.isEmpty(invalidSuffixes)) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.input.dnsSuffix.error");
      }
      return null;
   }

   adDomainValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const adDomain: string = control.value;
      if (this.getAdDomainValidationResult(adDomain)) {
         return {[this.INVALID_AD_DOMAIN_VALIDATION]: {value: adDomain}};
      }
      return null;
   };

   getAdDomainValidationResult(adDomain: string): string {
      if (!adDomain) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.noAdDomain");
      }
      if (!DomainValidationService.VALID_AD_CHARS.test(adDomain)) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.adDomain.invalidChars");
      }
      return null;
   }

   ouValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const ou: string = control.value;
      if (this.getOuValidationResult(ou)) {
         return {[this.INVALID_OU_VALIDATION]: {value: ou}};
      }
      return null;
   };

   getOuValidationResult(ou: string): string {
      // ou is not a must have, it can be unset
      if (!ou) {
         return null;
      }
      if (ou.length > this.ORGANIZATIONAL_UNIT_INPUT_MAXIMUM_LENGTH) {
         return VsanUiUtils.getString(
               "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.wholeInput.error.maxLength");
      }
      if (ou.startsWith(this.ORGANIZATIONAL_UNIT_HIERARCHICAL_SYMBOL)) {
         return VsanUiUtils.getString(
               "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.error.startWith");
      }
      if (ou.endsWith(this.ORGANIZATIONAL_UNIT_HIERARCHICAL_SYMBOL)) {
         return VsanUiUtils.getString(
               "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.error.endWith");
      }
      if (ou.includes(this.ORGANIZATIONAL_UNIT_HIERARCHICAL_SYMBOL)) {
         // ou with sub-ou, 'vmware/cpbu'
         const names: string[] = ou.split(this.ORGANIZATIONAL_UNIT_HIERARCHICAL_SYMBOL);
         if (names.some(name => !name)) {
            return VsanUiUtils.getString(
                  "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.error.containSlash");
         }
         if (names.some(name => VsanFileShareUtils.INVALID_OU_CHARS.test(name))) {
            return VsanUiUtils.getString(
                  "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.error.invalidChars");
         }
      } else if (ou.includes(this.OU_SEPARATOR)) {
         // ou with ad format, 'cn=something,ou=org,dc=example,dc=com'
         const parts: string[] = ou.split(this.OU_SEPARATOR).map(part => part.toLowerCase());
         const ouPart: string = parts.find(part => part.startsWith(this.OU_PREFIX));
         if (!ouPart) {
            return VsanUiUtils.getString(
                  "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.error.notExists");
         }
         const ouValue: string = ouPart.replace(this.OU_PREFIX, "");
         if (VsanUiUtils.isEmpty(ouValue)) {
            return VsanUiUtils.getString(
                  "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.error.notExists");
         }
         if (ouValue.length > this.ORGANIZATIONAL_UNIT_VALUE_MAXIMUM_LENGTH) {
            return VsanUiUtils.getString(
                  "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.error.maxLength");
         }
         if (VsanFileShareUtils.INVALID_OU_CHARS.test(ouValue)) {
            return VsanUiUtils.getString(
                  "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.error.invalidChars");
         }
      } else if (VsanFileShareUtils.INVALID_OU_CHARS.test(ou)) {
         return VsanUiUtils.getString(
               "vsan.services.fileservices.wizard.activeDirectoryPage.organizationalUnit.error.invalidChars");
      }
      return null;
   }

   usernameValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const username: string = control.value;
      if (this.getUsernameValidationResult(username)) {
         return {[this.INVALID_USERNAME_VALIDATION]: {value: username}};
      }
      return null;
   };

   getUsernameValidationResult(username: string): string {
      if (!username) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.nousername");
      }
      if (!DomainValidationService.VALID_AD_CHARS.test(username)) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.activeDirectoryPage.username.invalidChars");
      }
      return null;
   }

   /**
    * The suffix should be something like 'example.com' or 'eng.example.com'
    * true if it follows the pattern, otherwise false
    */
   private validateSingleSuffix(suffix: string): boolean {
      // we allow empty suffixes
      if (suffix === "") {
         return true;
      }
      const suffixValidator: RegExp = /^[\w.-]+(?:\.[\w.-]+)+[\w\-._~]+$/;
      return suffixValidator.test(suffix);
   }

   maskValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const mask: string = control.value;
      if (this.getMaskValidationResult(mask)) {
         return {[this.INVALID_MASK_VALIDATION]: {value: mask}};
      }
      return null;
   };

   prefixLengthValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const prefixLength: number = control.value;
      if (this.getPrefixLengthValidationResult(prefixLength)) {
         return {[this.INVALID_PREFIX_LENGTH_VALIDATION]: {value: prefixLength}};
      }
      return null;
   };

   getMaskValidationResult(mask: string): string {
      if (!mask) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.validation.mask.empty");
      }
      if (!this.ipParser.isSubnetMaskValid(mask)) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.validation.mask.invalid");
      }
      return null;
   }

   getPrefixLengthValidationResult(prefixLength: number): string {
      if (prefixLength < 1 || prefixLength > 128) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.validation.prefixLength.invalid");
      }
      return null;
   }
}
