import {Component, OnInit, ViewChild} from "@angular/core";
import {AbstractControl, FormBuilder, FormControl, FormGroup, ValidationErrors, ValidatorFn} from "@angular/forms";
import {ClrForm} from "@clr/angular";
import {InventorySelectionEvent} from "@component/inventory/inventory-node";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {PageItem} from "@component/wizard/page-item";
import {InventoryEntryData} from "@generated/inventory-entry-data";
import {NetworkInventoryService} from "@generated/network-inventory-service";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanFileServiceCommonConfig} from "@generated/vsan-file-service-common-config";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {VsanFileServiceIpType} from "@generated/vsan-file-service-ip-type";
import {VsanFileServicePrecheckResult} from "@generated/vsan-file-service-precheck-result";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {IpParserService} from "@service/ip-parser.service";
import {ManagedObject} from "@service/managed-object";
import {Icon} from "@util/icon";
import {ReferenceWatcher} from "@util/reference-watcher";
import {VsanUiUtils} from "@util/vsan-util";
import {DomainValidationService} from "./domain-validation.service";
import {IP} from "./ip.data";

@WizardPage(
      VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.title"),
)
@Component({
   templateUrl: "file-services-network-page.html",
   styleUrls: ["file-services-network-page.scss"],
})
export class FileServicesNetworkPageComponent implements OnValidate, OnInit {
   private readonly DOCKER_INTERNAL_IP_START: string = "172.17.";
   readonly INVALID_GATEWAY_VALIDATION: string = "invalidGateway";

   @InputProperty()
   @OutputProperty()
   fileServiceConfig: VsanFileServiceCommonConfig;

   @InputProperty()
   clusterRef: ManagedObject;

   @InputProperty()
   isEdit: boolean; // isEdit = true, when the user started the Edit workflow

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   capabilityData: VsanCapabilityData;

   @OutputProperty()
   networkName: string;

   @OutputProperty()
   protocol: IP;

   @ViewChild(ClrForm)
   clrForm: ClrForm;

   @LoadingIndicator()
   loading: boolean;

   objRefs: ManagedObject[];
   preselectedNode: ManagedObject;
   protocolList: IP[];
   showNetworkBrowser: boolean = false;
   displaySubnetConflictWarning: boolean = false;
   selectedNetworkNode: InventoryEntryData;
   numberOfHosts: number = 0;
   Icon: typeof Icon = Icon;
   IP: typeof IP = IP;
   networkForm: FormGroup;

   private ignoreSubnetConflict: boolean = false;
   private precheckResult: VsanFileServicePrecheckResult;

   constructor(private dataService: DataServiceClient,
         private fileService: VsanFileServiceConfigService,
         public networkInventoryService: NetworkInventoryService,
         public validationService: DomainValidationService,
         private formBuilder: FormBuilder,
         private ipParser: IpParserService,
         private refWatcher: ReferenceWatcher) {
   }

   async ngOnInit(): Promise<void> {
      this.protocolList = [IP.v4, IP.v6];
      this.objRefs = [this.clusterRef];
      this.preselectedNode = this.fileServiceConfig.network;
      this.networkName = "";
      this.protocol = this.fileServiceConfig.domainConfig.ipType === VsanFileServiceIpType.V6 ? IP.v6 : IP.v4;
      await this.getPrecheckResult();
      this.initFormGroup();
      if (!this.preselectedNode) {
         return;
      }
      try {
         this.pageControls.setBusyState();
         this.networkName = await this.networkInventoryService.getNetworkName(this.preselectedNode);
         // empty network name means the preselected network was removed,
         // so we need to clear the network setting, so user can select a new one.
         if (!this.networkName) {
            this.fileServiceConfig.network = null;
         }
      } catch (error) {
         this.pageControls.addValidationErrors([error]);
      } finally {
         this.pageControls.clearBusyState();
      }
   }

   @Loader()
   async getPrecheckResult(): Promise<void> {
      this.pageControls.setBusyState();
      this.precheckResult = await this.fileService.getPrecheckResult(this.clusterRef,
            this.fileServiceConfig.domainConfig.name
                  ? this.fileServiceConfig.domainConfig
                  : null,
            false);
      this.pageControls.clearBusyState();
   }

   selectNode(event: InventorySelectionEvent): void {
      if (event.node.data.isLeafNode) {
         this.fileServiceConfig.network = event.node.data.nodeRef;
         this.selectedNetworkNode = event.node.data;
         this.networkName = this.selectedNetworkNode ? this.selectedNetworkNode.name : "";
         this.showNetworkBrowser = false;
         this.invalidate();
      }
   }

   invalidate(): void {
      this.pageControls.rebuildToc();
   }

   ipV6Supported(): boolean {
      return this.precheckResult?.ipv6Supported;
   }

   getIpProtocolVersionText(protocol: IP): string {
      return this.isIpv4(protocol)
            ? VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.input.vlan.protocol.ipv4")
            : VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.input.vlan.protocol.ipv6");
   }

   changeProtocol(value: string | IP): void {
      if (typeof (value) === "string") {
         this.protocol = IP.parse(value as string);
      } else {
         this.protocol = value as IP;
      }

      this.fileServiceConfig.domainConfig.ipType = this.protocol.toVsanIpVersion();
      this.initFormGroup();
      this.invalidate();
   }

   inlineValidate(): boolean {
      this.clrForm.markAsTouched();
      // Only for ipv4 because it's not a clr-input-container, and cannot be covered in the above line.
      // If ipv6 the ipv6 gateway input is a standard clr-input-container element.
      if (this.protocol === IP.v4) {
         this.gatewayControl.markAsTouched();
      }
      return this.networkForm.valid;
   }

   validate(): string[] {
      const validationError: string = this.validateNetwork();
      return validationError ? [validationError] : null;
   }

   onSubmitConflictIgnored(): void {
      this.ignoreSubnetConflict = true;
      this.displaySubnetConflictWarning = false;
      (this.pageControls as PageItem).nextButtonIsDisabled = false;
   }

   private validateNetwork(): string {
      if (!this.fileServiceConfig.network) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.validation.network.empty");
      }

      return null;
   }

   private initFormGroup(): void {
      this.networkForm = this.getNetworkForm();
      this.refWatcher.watchSubscriptionRef(this.networkForm.valueChanges.subscribe(() => this.invalidate()));
      if (this.isIpv4()) {
         this.refWatcher.watchSubscriptionRef(
               this.maskControl.valueChanges.subscribe(value => this.fileServiceConfig.domainConfig.mask = value));
         this.refWatcher.watchSubscriptionRef(
               this.gatewayControl.valueChanges.subscribe(value => {
                  this.fileServiceConfig.domainConfig.gatewayAddress = value;
                  this.dockerInternalSubnetConflictCheck();
               }));
      } else {
         this.refWatcher.watchSubscriptionRef(
               this.prefixLengthControl.valueChanges
                     .subscribe(value => this.fileServiceConfig.domainConfig.prefixLength = value));
         this.refWatcher.watchSubscriptionRef(
               this.gatewayControl.valueChanges.subscribe(value =>
                     this.fileServiceConfig.domainConfig.ipv6GatewayAddress = value,
               ));
      }
   }

   private getNetworkForm(): FormGroup {
      return this.isIpv4()
            ? this.formBuilder.group({
               protocol: new FormControl(this.fileServiceConfig.domainConfig.ipType),
               gateway: new FormControl(
                     this.fileServiceConfig.domainConfig.gatewayAddress, this.gatewayValidator),
               mask: new FormControl(this.fileServiceConfig.domainConfig.mask, this.validationService.maskValidator),
            })
            : this.formBuilder.group({
               protocol: new FormControl(this.fileServiceConfig.domainConfig.ipType),
               gateway: new FormControl(
                     this.fileServiceConfig.domainConfig.ipv6GatewayAddress, this.gatewayValidator),
               prefixLength: new FormControl(
                     this.fileServiceConfig.domainConfig.prefixLength,
                     this.validationService.prefixLengthValidator),
            });
   }

   /**
    * Check if the configured network gateway and subnet mask has overlap with the docker internal network
    */
   private dockerInternalSubnetConflictCheck(): void {
      // The docker internal network is 172.17.0.0/16 (172.17.0.1 to 172.17.255.255), which is hard-coded in docker.
      // The easiest way to detect conflict is we just compare the first 2 octets.
      if (!this.ignoreSubnetConflict
            && this.fileServiceConfig.domainConfig.gatewayAddress.startsWith(this.DOCKER_INTERNAL_IP_START)) {
         this.displaySubnetConflictWarning = true;
         (this.pageControls as PageItem).nextButtonIsDisabled = true;
         return;
      }
      this.displaySubnetConflictWarning = false;
      (this.pageControls as PageItem).nextButtonIsDisabled = false;
   }

   private isIpv4(protocol?: IP): boolean {
      return (protocol || this.protocol) === IP.v4;
   }

   get networkNameText(): string {
      if (this.networkName) {
         return this.networkName;
      }
      return VsanUiUtils.getString("vsan.common.select");
   }

   get maskControl(): FormControl {
      return this.networkForm.get("mask") as FormControl;
   }

   get prefixLengthControl(): FormControl {
      return this.networkForm.get("prefixLength") as FormControl;
   }

   get gatewayControl(): FormControl {
      return this.networkForm.get("gateway") as FormControl;
   }

   get showGatewayControlError(): boolean {
      return this.gatewayControl.touched && !!this.gatewayControl.errors;
   }

   private gatewayValidator: ValidatorFn = (control: AbstractControl): ValidationErrors => {
      const gateway: string = control.value;
      if (this.getGatewayValidationResult(gateway)) {
         return {[this.INVALID_GATEWAY_VALIDATION]: {value: gateway}};
      }
      return null;
   };

   getGatewayValidationResult(gateway: string): string {
      if (!gateway) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.validation.gateway.empty");
      }
      const validator: (address: string) => boolean = this.protocol === IP.v4
            ? this.ipParser.isIpv4AddressValid
            : this.ipParser.isIpv6AddressValid;
      if (!validator(gateway)) {
         return VsanUiUtils.getString("vsan.services.fileservices.wizard.ipPool.validation.gateway.invalid");
      }
   }
}
